<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Reset Password</h1>
       <div class="chartWrap">
        </div>
    </section>
    <div class="center">
        <van-field v-model="pwd" type="password" placeholder="Enter new password" />
        <van-field v-model="confirmPwd" type="password" placeholder="Re-enter new password" />
    </div>
    
    <van-button type="danger" @click="submitReset">Save</van-button>
</div>
</template>

<script>

import {resetPassword} from '@/api/user'
export default {
  name: 'setPwd',
  data () {
    return {
          username:this.$route.query.username,
          pwd:"",
          confirmPwd:""
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    submitReset(){
        resetPassword({
          password:this.pwd,
          rePassword:this.confirmPwd
        }).then(res=>{
            this.$toast('Password has been reset successfully');
            this.$router.push('login');
        }).catch(e=>{
          // this.$toast(e.message);
        })
    }


  }
}
</script>

<style lang="scss" scoped>
    .center{
        width: 100%;
        padding: 0 6%;
        border-top: 10px solid #f4f4f4;
    }
    .van-button--danger{
        width: 92%;
        margin-left: 4%;
        margin-top: 102px;
    }
    .van-cell{
        padding-top: 30px;
        padding-left: 0;
        border-bottom: solid 1px #f2f3f5;
    }
</style>



